@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
}


.wrapper{
    width: 85vw;
    margin: 0 auto;
}

.home{
    height: 90vh;
    display: grid;
    place-content: center;
}
.home__component{
    flex-wrap: wrap;
}

@media only screen and (max-width: 800px) {
    .home__component{
        flex-direction: column;
        grid-gap: 2rem;
        gap: 2rem;
    }
  }
.navbar{
    margin-top: 2rem;
}


.flex{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
li{
    list-style: none;
}
.navbar li{
   
    font-size:  .75rem;
    margin-right: 1rem;
}

a{
    text-decoration: none;
    cursor: pointer;
    color: #fff;
    letter-spacing: .15rem;
    font-weight: bold;
}
a + a {
    margin: 0 1rem;
}

.nav__link:hover{
    color: #5199ec;
}
body{
    background-color: #15202b;
    color: #fff;
    font-family: 'Russo One', sans-serif;
}

.aboutMe{
    flex-basis: 35%;
}

.name{
    text-transform: uppercase;
    font-size: 2rem;
}
.profession{
    letter-spacing: .1rem;
    font-size: 1rem;
    color: #5199ec;
}
.aboutMe .description{
    margin-top: 2rem;
}

.aboutSkill{
    height: 175px;
}

.title-2{
   text-transform: uppercase;
    margin: .1rem 0;
    font-size: 1.15rem;
}

.skillItem{
    margin-right: 1rem;
    font-size: 1rem;
    color: #5199ec;
}
.indicators{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    position: relative;
    z-index: -10;
    margin-right:  2rem;
}
.indicators::before{
    content:  '';
    position: absolute;
    width: 2px;
    height: 100%;
    background-color:  #fff;
    display: block;
    top: 0;
    left: 50%;
    transform: translate(-50%,-0.2%);
    z-index: -10;
}
.indicator{
    background-color: #fff;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    z-index: 10;
}

.indicator:last-child{
    position: relative;
}
.indicator:last-child::before{
    content:  '';
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 1px solid #fff;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.skills{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
:root{
  --project-width: 350px;
}
body{
  position: relative;
  overflow: hidden;
}
.scene {
  width: 90%;
  margin: 10rem auto;
  height: 570px;
  
}
.slide {
  transform: scale(0.2);
  transition: transform 300ms;
  opacity: 0.5;
  position: relative;
  width: 500px;
  height: 300px;
}

.slide img {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  object-fit: cover;
}



.activeSlide {
  transform: scale(1.1);
  opacity: 1;
}

.arrow {
  position: absolute;
  cursor: pointer;
  z-index: 10;
}

.arrow svg {
  transition: color 300ms;
}

.arrow svg:hover {
  color: #68edff;
}

.next {
  right: 0%;
  top: 50%;
}

.prev {
  left: 0%;
  top: 50%;
}
@media only screen and (max-width: 800px) {
  .next{
    right: -20px;
  }
  .prev{
    left: -20px;
  }
}

.slide .description{
  opacity: 0;
  position:absolute;
  background-color: rgba(0,0,0,.75);
  top: 0;
  bottom: 0;
  padding: 4rem 2rem;
  transition: opacity .5s ease;
}
.activeSlide:hover .description{
  
  opacity: 1;
}

.text{
  font-size: .75rem;
  margin: .5rem 0;
}
.button{
  background-color: #68edff;
  padding: 1rem;
  border-radius: 5px;
  border:none;
  outline: none;
}
.contact{
    flex-direction: column;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    height: 85vh;
}
.contact h2{
    color:#15202b;
}

form {
    background: #15202b;
    border: 2px solid #16444d;
    width: 40vw;
    min-width: 400px;
    min-height: 50vh;
    font-size: 1.1rem;
    font-family: 'Russo One', sans-serif;
    padding: 2rem;
}
@media only screen and (max-width: 600px) {
    form{
        width: 90vw;
    }
  }
input{
    width: 100%;
    background: #192734;
    outline: none;
    border: 2px solid #16444d;
    border-radius: 5px;
    margin: .5rem 0;
    padding: .4rem 0;
}
textarea{
    width: 100%;
    text-align: left;
    margin: .5rem 0;
}
.btn{
    display: block;
    width: 100%;
    background: #17a2b8;
    text-align: center;
    cursor: pointer;
    padding: .5rem 0;
}
